body { font-family: Verdana, "Helvetica Neue", Helvetica, Arial, sans-serif; font-size: 16px; background-color: #edf0f8; } article { line-height: 1.5; padding: 1em; } blockquote { font-size: 16px; border-left: 5px #3a5795 solid; } dd { padding-bottom: 1em; } dt { font-weight: bold; } fieldset { margin: 1em 0 1em 0; } figcaption { font-style: italic; } figure { margin-bottom: 1em; } h1,h2,h3,h4 { font-family: "Archivo Narrow", sans-serif; font-weight: 700; } h1 { color: #4c4c4c; } h2,h3,h4 { clear: both; color: #666; } hr { margin-top: 20px; margin-bottom: 20px; border: 0; border-top: 1px solid #ddddde; } legend { font-weight: bold; padding: 0 1em 0 1em; } nav.inside { width: 100%; height: 100%; } nav.inside a { color: #bba3cd; display: block; text-decoration: none; padding: 0.5em; font-weight: bold; font-size: smaller; margin-bottom: 2px; } nav.inside a:link,nav.inside a:visited { background: #3a5795; color: #edf0f8; display: block; } nav.inside a:hover { background: #009cde; color: white; } nav.inside a:active,nav.inside a.active { background: #009cde; color: white; } nav.current { background: white; color: black; } q { quotes: "“" "”" "‘" "’"; } q::before { content: open-quote; } q::after { content: close-quote; } span.form-control__button-text { color: white; } table { max-width: 100%; background-color: transparent; border-collapse: collapse; border-spacing: 0; } .address { font-size: smaller; } .green { color: green; } .blue { color: navy; } .purple { color: purple; } .red { color: red; } .pdf { width: 100%; height: 600px; } .pull-left { margin-right: 1em; } .pull-right { margin-left: 1em; } .zot { margin-left: 0; padding-left: 0; list-style: none; } .zot li { padding-left: 24px; background-image: url(/_assets/img/zot.png); background-repeat: no-repeat; background-position: 0 .5em; }
